.card {
  
  padding: 20px;
  background:#000000;
  border-radius: 2px;
  height: 325px;
  margin: 1rem;
  position: relative;
  width: 325px;
  -webkit-box-shadow: 0 3px 6px #303030, 0 3px 6px #303030;
          box-shadow: 0 3px 6px #303030, 0 3px 6px #303030;
  text-align: left;
  border-radius: 25px;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:black;
  border-radius: 25px;
}
.card > .img-container > img {
  width: 100%
}


.card:hover
{
        -webkit-transform: scale(1.05);
        -ms-transform: scale(1.05);
        transform: scale(1.05);
}


